<template>
  <div>
    <h1>Snake Game with 
      <div class="geeks">u</div>
    </h1>
    <canvas id="board"></canvas>
  </div>
  
</template>

<script>
  export default {
    data(){
      return {
        blockSize: 25,
        total_row: 17, //total row number
        total_col: 17, //total column number
        board: null,
        context: null,

        snakeX: null,
        snakeY: null,

        // Set the total number of rows and columns
        speedX: 0, //speed of snake in x coordinate.
        speedY: 0, //speed of snake in Y coordinate.

        snakeBody: [],

        foodX: null,
        foodY: null,

        gameOver: false,
      }
    },
    methods: {
      update() {
      if (this.gameOver) {
        return;
      }

      // Background of a Game
      this.context.fillStyle = "black";
      this.context.fillRect(0, 0, this.board.width, this.board.height);

      // Set food color and position
      this.context.fillStyle = "white";
      this.context.fillRect(this.foodX, this.foodY, this.blockSize, this.blockSize);

      if (this.snakeX == this.foodX && this.snakeY == this.foodY) {
        this.snakeBody.push([this.foodX, this.foodY]);
        this.placeFood();
      }

      // body of snake will grow
      for (let i = this.snakeBody.length - 1; i > 0; i--) {
        // it will store previous part of snake to the current part
        this.snakeBody[i] = this.snakeBody[i - 1];
      }
      if (this.snakeBody.length) {
        this.snakeBody[0] = [this.snakeX, this.snakeY];
      }

      this.context.fillStyle = "orange";
      this.snakeX += this.speedX * this.blockSize; //updating Snake position in X coordinate.
      this.snakeY += this.speedY * this.blockSize; //updating Snake position in Y coordinate.
      this.context.fillRect(this.snakeX, this.snakeY, this.blockSize, this.blockSize);
      for (let i = 0; i < this.snakeBody.length; i++) {
        this.context.fillRect(this.snakeBody[i][0], this.snakeBody[i][1], this.blockSize, this.blockSize);
      }

      if (this.snakeX < 0 
        || this.snakeX > this.total_col * this.blockSize 
        || this.snakeY < 0 
        || this.snakeY > this.total_row * this.blockSize) { 
        
        // Out of bound condition
        this.gameOver = true;
        alert("Game Over");
      }

      for (let i = 0; i < this.snakeBody.length; i++) {
        if (this.snakeX == this.snakeBody[i][0] && this.snakeY == this.snakeBody[i][1]) { 
          
          // Snake eats own body
          this.gameOver = true;
          alert("Game Over");
        }
      }
    },

    // Movement of the Snake - We are using addEventListener
    changeDirection(e) {
      if (e.code == "ArrowUp" && this.speedY != 1) { 
        // If up arrow key pressed with this condition...
        // snake will not move in the opposite direction
        this.speedX = 0;
        this.speedY = -1;
      }
      else if (e.code == "ArrowDown" && this.speedY != -1) {
        //If down arrow key pressed
        this.speedX = 0;
        this.speedY = 1;
      }
      else if (e.code == "ArrowLeft" && this.speedX != 1) {
        //If left arrow key pressed
        this.speedX = -1;
        this.speedY = 0;
      }
      else if (e.code == "ArrowRight" && this.speedX != -1) { 
        //If Right arrow key pressed
        this.speedX = 1;
        this.speedY = 0;
      }
    },

    // Randomly place food
    placeFood() {

      // in x coordinates.
      this.foodX = Math.floor(Math.random() * this.total_col) * this.blockSize; 
      
      //in y coordinates.
      this.foodY = Math.floor(Math.random() * this.total_row) * this.blockSize; 
    }
    },
  mounted(){
    // Set board height and width
    this.board = document.getElementById("board");
    this.board.height = this.total_row * this.blockSize;
    this.board.width = this.total_col * this.blockSize;
    this.context = this.board.getContext("2d");
    var sx = this.blockSize * 5;
    var sy = this.blockSize * 5;

    this.placeFood();
    document.addEventListener("keyup", this.changeDirection); //for movements
    // Set snake speed
    setInterval(this.update, 1000 / 10);
  }
};
</script>

<style>/* Write CSS Here */
body {
	text-align: center;
}
.geeks {
	font-size: 40px;
	font-weight: bold;
	color: green;
}
#board{
  margin-left:auto;
  margin-right:auto;
}

</style>
